@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,400;0,700;1,500&display=swap);
* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    font-family: 'Poppins', sans-serif;
}
a {
    text-decoration: none;
}
.container {
    margin: 0 200px;
    height: 90vh;
}
body {
    background-color: #19303f;
}
h3 {
    color: #fff;
    font-size: 27px;
}
.front, .back , .base {
    display: flex;
    flex-direction: column;
    
}
.front-img, .back-img , .base-img{
    display: flex;
}
.habilidades-title {
    color: #fff;
    display: flex;
    justify-content: center;
    margin-top: 50px;
}
.habilidades-title h1 {
    border-bottom: 5px solid #CD5A69;
}
.lidades {
    color: #CD5A69;
}
.habilidades {
    margin-top: 50px;
    height: 60vh;
    display: flex;
    text-align: center;
    justify-content: space-around;
}
.habilidades img {
    width: 70px;
    margin-top:40px;
}
@media  screen and (max-width: 1700px){
    .container {
        height: 100vh!important;
    }
    h3{
        font-size: 20px;
    }
}
@media  screen and (max-width: 1300px) {
    .container {
        height: 80vh;
    }
    h3{
        font-size: 20px;
    }
}
@media  screen and (max-width: 1024px) {
    .container {
        margin: 0 40px!important;
        height: 80vh;
    }
    .otros h3{
        font-size: 20px;
    }
    img {
        margin-right: 20px;
    }
    .otros-img {
        margin-left: 10vw;
    }
    
}

@media  screen and  (max-width: 768px) {
.container{
    height: 310vh!important;
}
h3 {
    font-size: 19px;
}
.habilidades {
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
}
.otros {
    margin: 0 80px 0 0 ;
}
.otros h3 {
    margin-left: 20px;
}

}



